<template>
  <div class="box">
    <div class="personinfo">
      <img src="@/assets/personal/avatar.png" alt />
      <div>
        <div>
          <span class="username">小橘子</span>
          <span class="vip">普通会员</span>
        </div>
        <div class="referrer">推荐人：13422028759</div>
      </div>
      <div class="code" @click="togo('generalize')">
        <img src="@/assets/personal/code.png" alt />
        推广码
      </div>
    </div>
    <div class="belonging">
      <div class="b-item" @click="togo('myIntegral')">
        <p>1980</p>
        <p>积分</p>
      </div>
      <div class="b-item" @click="togo('income')">
        <p>￥200.30</p>
        <p>累计收益</p>
      </div>
      <div class="b-item" @click="togo('myPurse')">
        <p>￥100.00</p>
        <p>余额</p>
      </div>
    </div>
    <div class="navs">
      <ul>
        <li v-for="(item,index) in navsList" :key="index" @click="togo(item.url)">
          <img :src="item.iconurl" :alt="item.name" />
          {{ item.name }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navsList: [
        {
          name: "我的订单",
          iconurl: require("@/assets/personal/icon_order2.png"),
          url: "myOrders"
        },
        {
          name: "我的团队",
          iconurl: require("@/assets/personal/icon_assembly.png"),
          url: "myTeam"
        },
        {
          name: "收获地址",
          iconurl: require("@/assets/personal/icon_add.png"),
          url: "myAddress"
        },
        {
          name: "联系客服",
          iconurl: require("@/assets/personal/icon_contact.png"),
          url: "contact"
        },
        {
          name: "关于悦享美",
          iconurl: require("@/assets/personal/icon_about.png"),
          url: "about"
        }
      ]
    };
  },
  methods: {
    togo(url) {
      this.$router.push({
        name:url
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.box {
  background: url("../../../assets/personal/bg.png") no-repeat;
  background-size: 100%;
  overflow: hidden;
  .personinfo {
    margin-top: 83px;
    height: 105px;
    padding-left: 30px;
    display: flex;
    position: relative;
    img {
      height: 100px;
      width: 100px;
    }
    & > div {
      margin-left: 20px;
      color: #fff;
      height: 48px;
      line-height: 48px;
      // box-sizing: border-box;
      // padding: 10px;
      .username {
        font-size: 34px;
        // font-weight: bold;
      }
      .vip {
        background-color: #d9c186;
        font-size: 20px;
        margin-left: 10px;
        padding: 2px 10px;
        border-radius: 60px;
      }
    }
    .referrer {
      margin: 11px 0 0 5px;
      font-size: 24px;
      font-family: PingFang SC;
      font-weight: 400;
    }
    .code {
      width: 167px;
      height: 88px;
      background-color: rgba(51, 51, 51, 0.15);
      border-top-left-radius: 60px;
      border-bottom-left-radius: 60px;
      position: absolute;
      right: 0;
      bottom: 0;
      font-size: 26px;
      color: #fff;
      display: flex;
      align-items: center;
      img {
        height: 44px;
        width: 44px;
        margin: 0 14px 0 20px;
      }
    }
  }
  .belonging {
    margin: 46px 24px 20px;
    display: flex;
    background-color: #fff;
    height: 200px;
    align-items: center;
    border-radius: 10px;
    .b-item {
      flex: 1;
      // height: 72px;
      border-right: 1px solid #d7d7d7;
      &:last-of-type {
        border: none;
      }
      p {
        text-align: center;
        &:first-of-type {
          height: 45px;
          line-height: 45px;
          font-size: 32px;
          color: #ff4a54;
          margin-bottom: 6px;
        }
        &:last-of-type {
          height: 37px;
          line-height: 37px;
          font-size: 26px;
          color: #222;
        }
      }
    }
  }
  .navs {
    background-color: #fff;
    margin: 0 24px;
    border-radius: 10px;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        width: 25%;
        text-align: center;
        height: 187px;
        font-size: 26px;
        color: #555;
        img {
          margin: 34px auto 14px;
          display: block;
          width: 60px;
          height: 60px;
        }
      }
    }
  }
}
</style>



